@import url(https://fonts.googleapis.com/css2?family=Maven+Pro&display=swap);
.ace__about{
    display: flex;
    align-content: flex-start;
    justify-content: center;
    flex-direction: column;
}

.ace__about-content{
    display: flex;
    flex-direction: row;
    padding: 4rem 0rem;
}

@media screen and (max-width: 768px) {
    .ace__about-content{
    flex-direction: column;
    }
    
}

.ace__about-text{
    flex: 0.75 1;
    padding: 0rem 4rem;
}

.ace__about-text p{
font-family: var(--font-family);
color: var(--color-text);
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 24px;
text-align: justify;
padding-bottom: 2rem;
}

.ace__about-social h4{
font-family: var(--font-family);
color: var(--color-text);
font-style: normal;
font-weight: 800;
font-size: 14px;
line-height: 19px;
text-align: justify;
text-transform: uppercase;
padding-bottom: 1rem;

}

@media screen and (max-width:768px){
    .ace__about-social{
        padding: 0rem 4rem;
    }
    
}

@media screen and (max-width:560px) {
    .ace__about-text {
        padding: 0rem 0rem;

    }

    .ace__about-social {
        padding: 0rem 0rem;

    }
    
}
.ace__brand{
    display: flex;
    flex-direction: row;
    padding: 1rem;
}

.ace__brand p{
font-family: var(--font-family);
color: var(--color-text);
font-style: normal;
font-weight: 500;
font-size: 14px;
line-height: 20px;
text-align: justify;
padding: 0rem 1rem;
}

.ace__brand img{
    height: 24px;
}
.ace__brand-small{
    display: flex;
    flex-direction: row;
    padding: 0.25rem;
}

.ace__brand-small p{
font-family: var(--font-family);
color: var(--color-text);
font-style: normal;
font-weight: 500;
font-size: 14px;
line-height: 20px;
text-align: justify;
padding: 0rem 1rem;
}


.ace__brand-small img{
    height:16px;
}
.ace__heading{
    display: flex;
    flex-direction: column;
}

.ace__heading-title h2{
font-family: var(--font-family);
color: var(--color-text);
font-style: normal;
font-weight: 400;
font-size: 14px;
line-height: 21px;
text-transform: uppercase;
}

.ace__heading-sub h3{
font-family: var(--font-family);
color: var(--color-text);
font-style: normal;
font-weight: 500;
font-size: 36px;
line-height: 56px;
color: var(--color-text);
}



.ace__navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 6rem;

}

.ace__navbar img {
    width: 234px;
    height: 34px;
    justify-content: flex-start;
    align-items: center;
}

.ace__navbar-links{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    padding-left: 2rem;
}

.ace__navbar-links p{
    color: var(--color-text);
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 400;
    padding: 1rem;
}

.ace__navbar-menu{
    margin-left: 1rem;
    display: none; 
    position: relative;
}

.ace__navbar-menu__container{
    display:flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background-color: var(--color-bg);
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
}

.ace__navbar-menu p{
    color: var(--color-text);
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 400;
    padding: 1rem;
}

@media screen and (max-width: 991px){
    .ace__navbar{
        padding: 1rem;
        margin: 1rem;
    }

    
    .ace__navbar-links{
        display: none;
    }

    .ace__navbar-menu{
        display: flex;
    }
    
}

.ace__skills-brand{
    padding: 4rem 4rem;
    display: flex;
    flex-wrap: wrap;
}

@media screen and (max-width: 768px){
    .ace__skills-brand{
        padding: 2rem 2rem;
    }
}

.ace__home{
    display:flex;
}

.ace__home-content{
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    padding: 0rem 0rem 0rem 6rem;

}

.ace__home-content h1{
    font-family: var(--font-family);
    color: var(--color-text);
    font-style: normal;
    font-weight: 800;
    font-size: 96px;
    line-height: 80px;
}

.ace__home-content p{
    font-family: var(--font-family);
    color: var(--color-text);
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    padding: 2rem 0rem;
}

.ace__home-content button{
    padding: 14px 44px;
    width: 180px;
    height: 50px;
    left: 20px;
    top: 20px;
    background: var(--color-sub);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 11px;
    font-family: var(--font-family);
    color: var(--color-bg);
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    border:none;
    text-transform: uppercase;

}

.ace__home-bg{
    display:flex;
    justify-content: flex-end;
    align-items: center;
    flex: 1 1;
}

.ace__home-bg img{
    width: 100%;
    height: 100%;
}

@media screen and ( max-width: 768px ) {
    .ace__home{
        flex-direction: column;
    }

    .ace__home-content{
        padding: 4rem 2rem 2rem 2rem;
    }

    .ace__home-content h1{
        font-size: 75px;
    }
}

@media screen and (max-width:425px) {
    .ace__home-content h1{
        font-size: 50px;
        line-height: 45px;
    }
    
}

*{
    box-sizing: border-box;
    scroll-behavior: smooth;
    margin: 0;
    padding: 0;
}

/*body*/
body {
background-color:  var(--color-bg);
} 

/*links*/
a {
    color: unset;
    text-decoration: none;
}

/*section padding and margin*/
.section__padding {
    padding: 4rem 6rem;
}

.section__padding-home{
    padding: 2rem 0rem;
}

@media screen and (max-width:768px) {
    .section__padding{
        padding: 2rem;
    }
    
}

.section__margin {
    padding: 0rem 0rem;
}


/*animation scale up */
.scale-up-tr {
	-webkit-animation: scale-up-tr 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: scale-up-tr 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

/* animation scale-up-tr */
 @-webkit-keyframes scale-up-tr {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
      -webkit-transform-origin: 100% 0%;
              transform-origin: 100% 0%;
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      -webkit-transform-origin: 100% 0%;
              transform-origin: 100% 0%;
    }
  }
  @keyframes scale-up-tr {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
      -webkit-transform-origin: 100% 0%;
              transform-origin: 100% 0%;
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      -webkit-transform-origin: 100% 0%;
              transform-origin: 100% 0%;
    }
  }

/* Background Gradient*/
  .ace__bg-gradient{
background:-o-linear-gradient(150deg, rgba(57, 34, 98, 1) 0%, rgba(30, 30, 30, 1) 24%);
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1E1E1E', endColorstr='#392262', GradientType=1 );
background:linear-gradient(150deg, rgba(57, 34, 98, 1) 0%, rgba(30, 30, 30, 1) 24%);
}
  
